import React, { useEffect, useState } from 'react';
import { onAuthStateChanged } from 'firebase/auth';
import { auth } from './firebase'; // Make sure to import auth from your firebase config file
import ExpenseTable from './components/ExpenseTable';
import Auth from './components/Auth'; // Import the Auth component
import { Button, Modal, Form, Row, Col } from 'react-bootstrap';
import './App.css';

const App = () => {
  const [expenses, setExpenses] = useState([]);
  const [deletedExpenses, setDeletedExpenses] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [currentExpense, setCurrentExpense] = useState({ Expense: '', Amount: '', Paid: '', Same: false });
  const [editingIndex, setEditingIndex] = useState(null);
  const [user, setUser] = useState(null); // New state to track user authentication

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser);
      if (currentUser) {
        // Load user-specific expenses from localStorage or a database
        const storedExpenses = localStorage.getItem(`expenses_${currentUser.uid}`);
        if (storedExpenses) {
          setExpenses(JSON.parse(storedExpenses));
        }
      } else {
        // Clear expenses when user logs out
        setExpenses([]);
      }
    });

    return () => unsubscribe(); // Cleanup subscription on unmount
  }, []);

  useEffect(() => {
    if (user) {
      localStorage.setItem(`expenses_${user.uid}`, JSON.stringify(expenses));
    }
  }, [expenses, user]);

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    const newValue = type === 'checkbox' ? checked : value;
    if (name === 'Same' && checked) {
      setCurrentExpense({ ...currentExpense, Same: checked, Paid: currentExpense.Amount });
    } else if (name === 'Same' && !checked) {
      setCurrentExpense({ ...currentExpense, Same: checked });
    } else {
      setCurrentExpense({ ...currentExpense, [name]: newValue });
    }
  };

  const handleSaveClick = () => {
    if (editingIndex !== null) {
      editExpense(editingIndex, currentExpense);
    } else {
      addExpense(currentExpense);
    }
    setShowModal(false);
    setCurrentExpense({ Expense: '', Amount: '', Paid: '', Same: false });
    setEditingIndex(null);
  };

  const handleClose = () => {
    setShowModal(false);
    setCurrentExpense({ Expense: '', Amount: '', Paid: '', Same: false });
    setEditingIndex(null);
  };

  const addExpense = (expense) => {
    const updatedExpenses = [...expenses, { ...expense, Difference: expense.Amount - expense.Paid }];
    setExpenses(updatedExpenses);
  };

  const editExpense = (index, updatedExpense) => {
    const updatedExpenses = expenses.map((expense, i) => {
      if (i === index) {
        const paid = updatedExpense.Paid !== undefined ? updatedExpense.Paid : expense.Paid;
        const difference = updatedExpense.Amount - paid;
        return { ...updatedExpense, Paid: paid, Difference: difference };
      }
      return expense;
    });
    setExpenses(updatedExpenses);
  };

  const deleteExpense = (index) => {
    const expenseToDelete = expenses[index];
    const updatedExpenses = expenses.filter((_, i) => i !== index);
    setExpenses(updatedExpenses);
    setDeletedExpenses([...deletedExpenses, expenseToDelete]);
  };

  const restoreLastDeleted = () => {
    if (deletedExpenses.length > 0) {
      const lastDeleted = deletedExpenses.pop();
      setExpenses([...expenses, lastDeleted]);
      setDeletedExpenses([...deletedExpenses]);
    }
  };

  const toggleSame = (index) => {
    const updatedExpenses = expenses.map((expense, i) => {
      if (i === index) {
        const updatedSame = !expense.Same;
        const updatedPaid = updatedSame ? expense.Amount : 0;
        const difference = expense.Amount - updatedPaid;
        return { ...expense, Same: updatedSame, Paid: updatedPaid, Difference: difference };
      }
      return expense;
    });
    setExpenses(updatedExpenses);
  };

  const handleAddClick = () => {
    setShowModal(true);
  };

  const handleEditClick = (index) => {
    setEditingIndex(index);
    setCurrentExpense(expenses[index]);
    setShowModal(true);
  };

  const handleLogout = () => {
    auth.signOut();
  };

  if (!user) {
    return <Auth />;
  }

  return (
    <div className="App">
      <h1>Welcome, {user.email}</h1>
      <Button variant="danger" onClick={handleLogout}>Logout</Button>
      <Button className="add-expense-button" variant="primary" onClick={handleAddClick}>Add Expense</Button>
      <ExpenseTable expenses={expenses} onEdit={handleEditClick} onDelete={deleteExpense} onToggleSame={toggleSame} />
      <Button variant="warning" onClick={restoreLastDeleted}>Undo Delete</Button>

      <Modal show={showModal} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>{editingIndex !== null ? 'Edit Expense' : 'Add Expense'}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Row>
              <Col>
                <Form.Group>
                  <Form.Label>Expense</Form.Label>
                  <Form.Control
                    type="text"
                    name="Expense"
                    placeholder="Expense"
                    value={currentExpense.Expense}
                    onChange={handleChange}
                    required
                  />
                </Form.Group>
              </Col>
              <Col>
                <Form.Group>
                  <Form.Label>Amount</Form.Label>
                  <Form.Control
                    type="number"
                    name="Amount"
                    placeholder="Amount"
                    value={currentExpense.Amount}
                    onChange={handleChange}
                    required
                  />
                </Form.Group>
              </Col>
              <Col>
                <Form.Group>
                  <Form.Label>Paid</Form.Label>
                  <Form.Control
                    type="number"
                    name="Paid"
                    placeholder="Paid"
                    value={currentExpense.Paid}
                    onChange={handleChange}
                    required
                    disabled={currentExpense.Same}
                  />
                </Form.Group>
              </Col>
              <Col>
                <Form.Group>
                  <Form.Check
                    type="checkbox"
                    label="Same"
                    name="Same"
                    checked={currentExpense.Same}
                    onChange={handleChange}
                    className="add-expense-checkbox"
                  />
                </Form.Group>
              </Col>
            </Row>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Cancel
          </Button>
          <Button variant="primary" onClick={handleSaveClick}>
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
};

export default App;
